.nav-bar {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 1.33333333rem;
  line-height: 1.33333333rem;
  z-index: 999;
  text-align: center;
  font-size: 0.53333333rem;
  background-color: #0088f9;
  color: #fff;
}

.left-icon {
  position: absolute;
  left: 0.26666667rem;
  line-height: 1.33333333rem;
}

.left-icon > svg {
  font-size: 0.4rem;
}